@mixin title {
    font-size: 35px;
    font-family: $font-title;
    text-align: center;
    color: $color-secondary-dark;
    text-transform: uppercase;
    margin-bottom: 60px; }

@mixin btn {
    color: $color-primary-dark;
    font-family: $font-title;

    padding: 15px 55px;

    transition: background-color .1s linear;

    border: 1px $color-secondary-dark solid;
    border-radius: 100px;

    background-color: $color-secondary-dark;

    &:hover {
        background-color: #f58d2b;
        border-color: #f58d2b; } }

